.books_book{
    width: 16%;
    height: 220px;
    background-color: black; 
    color: white;
    border: solid black 1px;
    display: inline-block;
    margin: 20px;
    position: relative;
    vertical-align:top;
}
.books_ima{
    width: 100%;
    height: 140px;
    text-align: center;
}
.books-image {
    width: auto;
    max-height: 100%;
}
.books_desc{
    width: 100%;
    background-color: black;
    color: white;
    height: 80px;
    font-size :12px;
    font-size: 1.25vw; 
    word-wrap: break-word;
}
