/*--------------------------------HEADER - NA-------------------------------------------------*/
header{
    overflow: hidden;
    background-color: #f1f1f1;
    padding: 10px 10px;
}
header a {
    float: left;
    color: black;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 18px; 
    line-height: 25px;
    border-radius: 4px;
  }
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

li{
    list-style-type: none;
    float: left;
    background-color: #b93e3e;
    border-radius: 15px 15px 0px 0px;
    margin: 0.5px;
}

li  a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #fff;
    color: #333;
    border-radius: 15px 15px 0px 0px;
}


/*--------------------------------FOOTER - NA-------------------------------------------------*/
footer{
    display: flex !important;
    flex-direction: row-reverse;
    justify-content: space-between;
    overflow: hidden;
    background-color: #b93e3e;
    padding: 10px 10px;
    color: #fff;
}
footer i {
    color:#fff;
    margin:5px;
}
.footer-socialmedia{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-right: 25px;
}